:root {
    --color-dark: #27292a;
    --color-darkest:#1f2122;
    --color-light: #fff;
    --color-primary: #ec6090;
    --color-icons: yellow;

    --color-bg-body:var(--color-darkest);

    --dist-primary:50px;
    --padding-primary:var(--dist-primary);
    --margin-primary:var(--dist-primary);
    --border-radius-primary:calc(var(--dist-primary) / 2);

    --font-family-primary:'Poppins', sans-serif;
    --font-weight-bold:900;
    --font-weight-normal:400;

}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700;900&family=Roboto:wght@300;400;700&display=swap');
body {
    background-color: var(--color-bg-body);
    font-family: var(--font-family-primary);
}
ul {
    list-style-type: none;
    text-decoration: none;
}
a {
    text-decoration: none;
}